import CardCon from '../components/CardCon';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import cssStyle from '../css/SpecialMore.module.css';

export default function SpecialMore() {
  let aniData = useSelector((a) => a.anniverData);
  const [activeButton, setActiveButton] = useState(''); //class on/off
  let [list, setList] = useState(aniData); // 

  return (
    <main className='mw'>
      <section className={`subSpeMore ${cssStyle.subAni}`}>
        <h2>2023   </h2>

        <div className={cssStyle.btns}>
          <button
            className={activeButton === '' ? cssStyle.on : ''}
            onClick={() => {
              setActiveButton('');
              setList([...aniData].sort((a, b) => b.title - a.title));
            }}
          >
            
          </button>
          <button
            className={activeButton === '' ? cssStyle.on : ''}
            onClick={() => {
              setActiveButton('');
              setList(
                [...aniData].sort(
                  (a, b) => a.startDate.localeCompare(b.startDate) // 
                )
              );
            }}
          >
            
          </button>
          <button
            className={activeButton === '' ? cssStyle.on : ''}
            onClick={() => {
              setActiveButton('');
              setList([...aniData].sort((a, b) => b.count - a.count));
            }}
          >
            
          </button>
        </div>

        <ul>
          {list.map((item) => (
            <CardCon item={item} />
          ))}
        </ul>
      </section>
    </main>
  );
}
