import { useState } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import cssStyle from '../css/RankingMore.module.css';

export default function RankingMore() {
  let totalData = useSelector((a) => a.datalist);
  let [rankData, setRankData] = useState(totalData); //filter
  let [activeButton, setActiveButton] = useState('');
  let navigate = useNavigate();

  return (
    <main className='mw'>
      <section className={cssStyle.subRank}>
        <h2> </h2>
        <nav>
          <ul>
            <li
              className={activeButton === '' ? cssStyle.on : ''}
              onClick={() => {
                setRankData(totalData);
                setActiveButton('');
              }}
            >
              
            </li>
            <li
              className={activeButton === ' / ' ? cssStyle.on : ''}
              onClick={() => {
                setRankData(
                  [...totalData].filter((a) => a.category === '' || a.category === '')
                );
                setActiveButton(' / ');
              }}
            >
               / 
            </li>
            <li
              className={activeButton === '' ? cssStyle.on : ''}
              onClick={() => {
                setRankData([...totalData].filter((a) => a.category === ''));
                setActiveButton('');
              }}
            >
              
            </li>
            <li
              className={activeButton === ' / ' ? cssStyle.on : ''}
              onClick={() => {
                setRankData(
                  [...totalData].filter((a) => a.category === '' || a.category === '')
                );
                setActiveButton(' / ');
              }}
            >
               / 
            </li>
            <li
              className={activeButton === ' / ' ? cssStyle.on : ''}
              onClick={() => {
                setRankData(
                  [...totalData].filter((a) => a.category === '' || a.category === '')
                );
                setActiveButton(' / ');
              }}
            >
               / 
            </li>
            <li
              className={activeButton === '' ? cssStyle.on : ''}
              onClick={() => {
                setRankData([...totalData].filter((a) => a.category === ''));
                setActiveButton('');
              }}
            >
              
            </li>
          </ul>
        </nav>
        <ul className={cssStyle.rankCon}>
          {rankData.map((item, i) => (
            <li
              key={item.id}
              onClick={() => {
                navigate(`/detail/${item.id}`);
              }}
            >
              <span className={cssStyle.num}>{i + 1}</span>
              <div className={cssStyle.imgCon}>
                <img src={process.env.PUBLIC_URL`/img/${item.image}`} alt={item.title} />
              </div>
              <strong>{item.title}</strong>
              <span className={cssStyle.place}>{item.place}</span>
              <span className={cssStyle.place}>
                {item.startDate === item.endDate
                  ? item.startDate
                  : `${item.startDate} ~ ${item.endDate}`}
              </span>
            </li>
          ))}
        </ul>
      </section>
    </main>
  );
}
