import BtnTotal from '../components/BtnTotal';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import cssStyle from '../css/Ranking.module.css';

export default function Ranking() {
  let totalData = useSelector((a) => a.datalist);
  let [rankData, setRankData] = useState(totalData);
  let [activeButton, setActiveButton] = useState('# '); //  on/off
  let navigate = useNavigate();

  return (
    <main className='mw'>
      <section className={cssStyle.sec3}>
        <div className='title'>
          <h2> </h2>
          <nav>
            <ul className={cssStyle.navList}>
              <li
                onClick={() => {
                  setRankData(totalData);
                  setActiveButton('# ');
                }}
                className={activeButton === '# ' ? cssStyle.on : ''}
              >
                # 
              </li>
              <li
                onClick={() => {
                  setRankData(
                    [...totalData]
                      .filter((a) => a.category === '' || a.category === '')
                      .sort((a, b) => b.count - a.count)
                  );
                  setActiveButton('# /');
                }}
                className={activeButton === '# /' ? cssStyle.on : ''}
              >
                # /
              </li>
              <li
                onClick={() => {
                  setRankData(
                    [...totalData]
                      .filter((a) => a.category === '')
                      .sort((a, b) => b.count - a.count)
                  );
                  setActiveButton('# ');
                }}
                className={activeButton === '# ' ? cssStyle.on : ''}
              >
                # 
              </li>
              <li
                onClick={() => {
                  setRankData(
                    [...totalData]
                      .filter((a) => a.category === '' || a.category === '')
                      .sort((a, b) => b.count - a.count)
                  );
                  setActiveButton('# /');
                }}
                className={activeButton === '# /' ? cssStyle.on : ''}
              >
                # /
              </li>
              <li
                onClick={() => {
                  setRankData(
                    [...totalData]
                      .filter((a) => a.category === '' || a.category === '')
                      .sort((a, b) => b.count - a.count)
                  );
                  setActiveButton('# /');
                }}
                className={activeButton === '# /' ? cssStyle.on : ''}
              >
                # /
              </li>
              <li
                onClick={() => {
                  setRankData(
                    [...totalData]
                      .filter((a) => a.category === '')
                      .sort((a, b) => b.count - a.count)
                  );
                  setActiveButton('# ');
                }}
                className={activeButton === '# ' ? cssStyle.on : ''}
              >
                # 
              </li>
            </ul>
          </nav>
          <Link to='/ranking'>
            <BtnTotal />
          </Link>
        </div>
        <ul className={cssStyle.listCon}>
          {rankData.slice(0, 8).map((item, i) => (
            <li className={cssStyle.rankList} key={item.id}>
              <div
                className={cssStyle.imgCon}
                onClick={() => {
                  navigate(`/detail/${item.id}`);
                }}
              >
                <img src={process.env.PUBLIC_URL`/img/${item.image}`} alt={item.title} />
                <span>{i + 1}</span>
              </div>
              <strong>{item.title}</strong>
              <span>
                {item.startDate === item.endDate
                  ? item.startDate
                  : `${item.startDate} ~ ${item.endDate}`}
              </span>
            </li>
          ))}
        </ul>
      </section>
    </main>
  );
}
